import React from "react"
import { Route, Redirect } from "react-router-dom"
import { urls } from "pages/urls"

export const LoggedOutRoute = ({ component: Component, path, loggedIn }) => {
  return (
    <Route
      render={p => {
        if (loggedIn) return <Redirect to={urls.home.url} />
        return <Component {...p} />
      }}
      path={path}
    />
  )
}
